import { Component, OnInit } from '@angular/core';
import { SparringOfferService } from '../../services/sparring-offer.service';
import { SparringScheduleService } from '../../services/sparring-schedule.service';
import { ToastyService, ToastyConfig } from 'ng2-toasty';
import { Router } from '@angular/router';
import { Mailer } from '../../mailer/mailer';

@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
    offers: any;
    bids: any;
    homeTeam_schedules: any;
    awayTeam_schedules: any;

    hasOffers: boolean;
    hasBids: boolean;
    awayHasSchedules: boolean;
    homeHasSchedules: boolean;

    // Property Penawaran dan tawaran
    offer_id: any;
    offer_team: string;
    bid_id: any;
    bid_team: any;

    // Properti Pernjadwalan
    away_team: string;
    id_away_team: any;
    away_email: string;
    myDate: any;
    nama_tempat: string;
    alamat: string;
    idJadwal: any;
    j_away: string;
    j_home: string;
    j_waktu: string;
    j_email: string;
    l_nama_tempat: string;
    l_alamat: string;


    // reschedule property
    myDate_res: any;
    nama_tempat_res: string;
    alamat_res: string;
    idJadwal_res: any;
    teamRespond: boolean;

    mailData: any;
    userLog: any;
    userData: any;
    // paginasi property
    p1: number = 1;
    p2: number = 1;
    d_jadwal: any;
  constructor(public sparrOffer: SparringOfferService,
      public jadwalSparr: SparringScheduleService,
      private toastMsg: ToastyService,
      private toastConf: ToastyConfig,
      private router: Router) {
          this.toastConf.theme = 'default';
      }

  ngOnInit() {
    this.loadData().then((res) => {
        console.log(res)
    }, err => {
      console.log(err)
    })
    this.userData = this.getUserData();
      // return new Promise((resolve, reject) => {
      //   this.loadOffers();
      //   this.loadBids();
      //   this.getJadwalSparring();
      //   this.getJadwalAwayTeam();
      //   setTimeout(() => {
      //        this.hasOffers = this.offers.length > 0 ? true : false;
      //        this.homeHasSchedules = this.homeTeam_schedules.length > 0 ? true : false;
      //   }, 1000);
      //   setTimeout(() => {
      //        this.hasBids = this.bids.length > 0 ? true : false;
      //        this.awayHasSchedules = this.awayTeam_schedules.length > 0 ? true : false;
      //   }, 1000);
      //   setTimeout(() => {
      //       this.userLog = JSON.parse(localStorage.getItem('user'))
      //       this.autoDelete();
      //   }, 900);
      // })
  }

  getUserData(){
    return JSON.parse(localStorage.getItem('user'))
  }

  loadData(){
    var promise = new Promise((resolve, reject) => {
      setTimeout(() => {
        this.loadOffers();
        this.loadBids();
        this.getJadwalSparring();
        this.getJadwalAwayTeam();
        setTimeout(() => {
          this.hasOffers = this.offers.length > 0 ? true : false;
          this.homeHasSchedules = this.homeTeam_schedules.length > 0 ? true : false;
          this.hasBids = this.bids.length > 0 ? true : false;
          this.awayHasSchedules = this.awayTeam_schedules.length > 0 ? true : false;
          this.userLog = JSON.parse(localStorage.getItem('user'))
          this.autoDelete();
        }, 1000)
      }, 1000)
    })
    return promise;
  }

  autoDelete(){ //Jika Penawaran telah masuk tahap Penjadwalan
      for(let i = 0;i < this.offers.length;i++){
          if(this.offers[i].status === 'Diproses'){
              this.sparrOffer.deleteOffer(this.offers[i]._id).subscribe(data => {
                  if(data.success){
                      this.loadOffers();
                      this.loadBids();
                  } else {
                      this.router.navigate(['/dashboard'])
                  }
              })
          }
      }
  }

  loadOffers(){
      this.sparrOffer.getOffers().subscribe(data => {
          if(data.success){
              this.offers = data.offers;
              console.log(data)
          }
      });
  }

  loadBids(){
      this.sparrOffer.getBids().subscribe(data => {
          if(data.success){
              this.bids = data.bids;
              console.log(data)
          }
      })
  }

  // Untuk Penawaran Sparring
  batalkanSparring(){
      const respon = {
          responTawaran: 'Dibatalkan'
      }

      this.sparrOffer.responPenawaran(this.offer_id, respon).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.loadOffers();
               this.loadData().then((res) => {
                    return;
                }, err => {
                  console.log(err)
                })
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.router.navigate(['/dashboard'])
          }
      })
  }

  konfirmPembatalan(offerId, offerTeam){
      this.offer_id = offerId;
      this.offer_team = offerTeam;
  }

  batalkanPenawaran(){
      let id = this.offer_id;
      this.sparrOffer.deleteOffer(id).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.loadOffers();
               this.loadData().then((res) => {
                console.log(res)
            }, err => {
              console.log(err)
            })
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.router.navigate(['/dashboard'])
          }
      })
  }

  hapusPenawaran(offerId){
      this.sparrOffer.deleteOffer(offerId).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.loadOffers();
               this.loadData().then((res) => {
                    console.log(res)
                }, err => {
                  console.log(err)
                })
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.router.navigate(['/dashboard'])
          }
      })
  }

  // Untuk Tawaran Sparring
  konfirmTerima(bidId, bidTeam){
      this.bid_id = bidId;
      this.bid_team = bidTeam;
  }

  terimaTawaran(){
      const respon = {
          responTawaran: 'Diterima'
      }

      this.sparrOffer.responTawaran(this.bid_id, respon).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.loadBids();
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.router.navigate(['/dashboard'])
          }
      })
  }


  konfirmTolak(bidId, bidTeam){
      this.bid_id = bidId;
      this.bid_team = bidTeam;
  }

  tolakTawaran(){
      const respon = {
          responTawaran: 'Ditolak'
      }

      this.sparrOffer.responTawaran(this.bid_id, respon).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.loadBids();
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.router.navigate(['/dashboard'])
          }
      })
  }

  // Penjadwalan Sparring
  getJadwalSparring(){ /* Untuk Home Team */
      this.jadwalSparr.getAllSchedule().subscribe(data => {
          if(data.success){
              this.homeTeam_schedules = data.schedules;
              //console.log(data)
          }
      })
  }

  getJadwalAwayTeam(){
      this.jadwalSparr.getJadwalAway().subscribe(data => {
          if(data.success){
              this.awayTeam_schedules = data.schedules;
              console.log(data);
          } else {
              console.log(data.err)
          }
      })
  }

  openModalPenjadwalan(offer){
      this.away_team = offer.away_team;
      this.id_away_team = offer.id_away_team;
      this.offer_id = offer._id;
      this.away_email = offer.away_email;
  }

  sendJadwal(){
      let jadwal = {
          away_team: this.away_team,
          id_away_team: this.id_away_team,
          waktu_pertandingan: this.myDate,
          lokasi_pertandingan: {
              nama_tempat: this.nama_tempat,
              alamat: this.alamat
          },
          away_email: this.away_email
      }

      this.mailData = {
        home: {
          receiver: this.userData.email,
          subject: 'Jadwal Sparring Diterima',
          text: 'Kamu Berhasil Membuat Jadwal Sparring'
        },
        away: {
          receiver: this.away_email,
          subject: 'Pertandingan Telah Dijadwalkan',
          text: 'Kamu Telah Mendapatkan Tawaran Jadwal Sparring'
        }
      }

      this.jadwalSparr.sendJadwal(jadwal).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.loadOffers();
              this.getJadwalSparring();
              this.getJadwalAwayTeam();
              this.sendEmail(this.mailData.home, this.mailData.away, 1)
              setTimeout(() => {
                  let respon = {
                      responTawaran: 'Diproses'
                  }

                  this.sparrOffer.responPenawaran(this.offer_id, respon).subscribe(data => {
                      if(data.success){
                          setTimeout(() => {
                          this.loadOffers();
                          this.getJadwalSparring();
                          this.getJadwalAwayTeam();
                          }, 500);
                          this.router.navigate(['/dashboard'])
                      }else {
                          console.log(data)
                      }
                  })
              }, 1000);
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              console.log(data)
              this.router.navigate(['/dashboard'])
          }
      })
  }

  konfirmasiJadwal(jadwalNya){
      this.idJadwal = jadwalNya._id;
      this.j_away = jadwalNya.away_team;
      this.j_home = jadwalNya.home_team;
      this.j_waktu = jadwalNya.waktu_pertandingan;
      this.l_nama_tempat = jadwalNya.lokasi_pertandingan.nama_tempat;
      this.l_alamat = jadwalNya.lokasi_pertandingan.alamat;
      this.j_email = jadwalNya.away_email
      this.d_jadwal = jadwalNya
  }

  sendEmail(mailHome, mailAway, state: number){
    switch (state) {
      case 1:
        this.jadwalSparr.sendMail(mailHome).subscribe(data => {
          if(data.success){
            console.log(data)
          } else {
            console.log(data)
          }
        })

        this.jadwalSparr.sendMail(mailAway).subscribe(data => {
          if(data.success){
            console.log(data)
          } else {
            console.log(data)
          }
        })
        break;
    }
  }

  terimaJadwal(){

      const respon = {
          respon: 'Diterima'
      }

      const newMatch = {
          home_team: this.j_home,
          away_team: this.j_away,
          waktu_pertandingan: this.j_waktu,
          lokasi_pertandingan: {
              nama_tempat: this.l_nama_tempat,
              alamat_tempat: this.l_alamat
          }
      }

      const team = {
        home: this.j_home,
        away: this.j_away
      }

      this.mailData = {
        home: {
          receiver: this.userData.email,
          subject: 'Pertandingan Telah Dijadwalkan',
          text: '<h2>Pertandingan Telah Dijadwalkan<h2><hr>'+ team.home +' VS '+ team.away +'  Detail Pertandingan: <br>'+new Date(this.d_jadwal.waktu_pertandingan)+'<br> at '+ this.d_jadwal.lokasi_pertandingan.nama_tempat +', '+ this.d_jadwal.lokasi_pertandingan.alamat
        },
        away: {
          receiver: this.j_email,
          subject: 'Pertandingan Telah Dijadwalkan',
          text: '<h2>Pertandingan Telah Dijadwalkan<h2><hr>'+ team.home +' VS '+ team.away +'  Detail Pertandingan: <br>'+this.d_jadwal.waktu_pertandingan+'<br> at '+ this.d_jadwal.lokasi_pertandingan.nama_tempat +', '+ this.d_jadwal.lokasi_pertandingan.alamat
        }
      }

      this.jadwalSparr.terimaJadwal(this.idJadwal, respon).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.jadwalSparr.storeSparrBoard(newMatch).subscribe(data => {
                  if(data.success){
                      this.toastMsg.info({
                          title: "Berhasil",
                          msg: "Pertandingan Telah di masukan ke Sparring Board",
                          showClose: true,
                          timeout: 3500
                      });
                      this.sendEmail(this.mailData.home, this.mailData.away, 1);
                  } else {
                      this.toastMsg.error({
                          title: "Oops..!",
                          msg : data.msg,
                          showClose: true,
                          timeout: 2700
                      });
                      this.router.navigate(['/dashboard']);
                  }
              })
              this.getJadwalAwayTeam();
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.router.navigate(['/dashboard']);
          }
      })
  }

  tolakJadwal(){
      const respon = {
          respon: 'Ditolak'
      }

      this.jadwalSparr.terimaJadwal(this.idJadwal, respon).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.getJadwalAwayTeam();
          } else {
              this.toastMsg.error({
                  title: "Oops..!",
                  msg: data.msg,
                  showClose: true,
                  timeout: 2500
              });
              this.router.navigate(['/dashboard']);
          }
      })
  }

  openRescheduler(jadwalSebelumnya){
      this.myDate_res = jadwalSebelumnya.waktu_pertandingan;
      this.nama_tempat_res = jadwalSebelumnya.lokasi_pertandingan.nama_tempat;
      this.alamat_res = jadwalSebelumnya.lokasi_pertandingan.alamat;
      this.idJadwal_res = jadwalSebelumnya._id;
      this.teamRespond = jadwalSebelumnya.team_respond;
  }

  rescheduling(){
    const newJadwal = {
          waktu_pertandingan: this.myDate_res,
          lokasi_pertandingan: {
            nama_tempat: this.nama_tempat_res,
            alamat: this.alamat_res
          },
          team_respond: this.teamRespond === false ? true: false
    }

    this.jadwalSparr.reschedule(this.idJadwal_res, newJadwal).subscribe(data => {
        if(data.success){
            this.toastMsg.success({
                title: "Berhasil",
                msg: data.msg,
                showClose: true,
                timeout: 2500
            });
            this.getJadwalAwayTeam();
            this.getJadwalSparring();
        } else {
            this.toastMsg.error({
                title: "Oops..!",
                msg: data.msg,
                showClose: true,
                timeout: 2500
            });
            console.log(data.err)
            this.router.navigate(['/dashboard']);
        }
    })

  }

}



