import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'teamsFilter'
})
export class TeamsFilterPipe implements PipeTransform {

  transform(teams: any, regional: any): any {

    // Cek Jika Regional adalah Semua
    if(regional === 'Semua') return teams;

    // Return data yg di filter
    return teams.filter(function(team){
        return team.regional.toLowerCase().includes(regional.toLowerCase());
    })
  }

}



