import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
import { RouterModule, Routes } from '@angular/router';
import { FlashMessagesModule } from 'angular2-flash-messages';
import { ToastyModule } from 'ng2-toasty';
import { Angular2FontAwesomeModule } from 'angular2-font-awesome/angular2-font-awesome';
import { NgxPaginationModule } from 'ngx-pagination';
import { NguiDatetimePickerModule } from '@ngui/datetime-picker';

import { AppComponent } from './app.component';
import { NavbarComponent } from './components/navbar/navbar.component';
import { HomeComponent } from './components/home/home.component';
import { LoginComponent } from './components/login/login.component';
import { RegisterComponent } from './components/register/register.component';

// Import Services
import { RegionalService } from './services/regional.service';
import { AuthService } from './services/auth.service';
import { ValidateService } from './services/validate.service';
import { SparingBoardService } from './services/sparing-board.service';
import { SparYukService } from './services/spar-yuk.service';
import { SparringOfferService } from './services/sparring-offer.service';
import { SparringScheduleService } from './services/sparring-schedule.service';

import { ProfileComponent } from './components/profile/profile.component';
import { SparingBoardComponent } from './components/sparing-board/sparing-board.component';

import { AuthGuard } from './guards/auth.guard';
import { SparYukComponent } from './components/spar-yuk/spar-yuk.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { TeamsFilterPipe } from './filters/teams-filter.pipe';

const AppRoutes: Routes = [
  {path:'', component:HomeComponent},
  {path:'login', component:LoginComponent},
  {path:'register', component:RegisterComponent},
  {path:'profile', component:ProfileComponent, canActivate:[AuthGuard]},
  {path:'dashboard', component:DashboardComponent, canActivate:[AuthGuard]},
  {path:'spar-yuk', component:SparYukComponent, canActivate:[AuthGuard]},
  {path:'sparing-board', component:SparingBoardComponent}
]

@NgModule({
  declarations: [
    AppComponent,
    NavbarComponent,
    HomeComponent,
    LoginComponent,
    RegisterComponent,
    ProfileComponent,
    SparingBoardComponent,
    SparYukComponent,
    DashboardComponent,
    TeamsFilterPipe
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(AppRoutes),
    FlashMessagesModule,
    ToastyModule.forRoot(),
    Angular2FontAwesomeModule,
    NgxPaginationModule,
    NguiDatetimePickerModule
  ],
  providers: [RegionalService, AuthService, ValidateService, SparingBoardService, SparYukService, SparringOfferService, SparringScheduleService, AuthGuard],
  bootstrap: [AppComponent]
})
export class AppModule { }



