import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { RegionalService } from '../../services/regional.service';
import { ValidateService } from '../../services/validate.service';
import { AuthService } from '../../services/auth.service';
import { ToastyService, ToastyConfig} from 'ng2-toasty';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  // User Property
  username: string;
  password: string;
  email: string;
  name: string;
  contact: string;
  usia_pemain: string;
  team_agent: string;
  regional: any;

  region: any;
  constructor(
      private regionalService: RegionalService,
      private validateService: ValidateService,
      private authService: AuthService,
      private router: Router,
      private toastMsg: ToastyService,
      private toastConf: ToastyConfig) {
          this.toastConf.theme = "default";
      }

  ngOnInit() {
      this.regionalService.loadRegional().subscribe(data => {
          this.region = data.regional;
      })
  }

  onRegister(){
      const user = {
          name: this.name,
          contact: this.contact,
          usia_pemain: this.usia_pemain,
          team_agent: this.team_agent,
          regional: this.regional,
          email: this.email,
          username: this.username,
          password: this.password,
      }

      // Cek Jika Field ada yang kosong!
      if(!this.validateService.validateRegister(user)){
          this.toastMsg.error({
                title: "Pengisian Belum Lengkap",
                msg: "Lengkapi Seluruh Field",
                showClose: true,
                timeout: 5000,
          })
          return false;
      }

      if(!this.validateService.validateEmail(user.email)){
          this.toastMsg.error({
                title: "Email not Valid",
                msg: "Masukan Email yang Valid!",
                showClose: true,
                timeout: 5000,
          })
          return false;
      }

      // registering user
      this.authService.registerUser(user).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: "Berhasil",
                  msg: data.msg,
                  showClose: true,
                  timeout: 3000
              });
              this.router.navigate(["/login"]);
          } else {
              this.toastMsg.error({
                  title: "Gagal",
                  msg: data.msg,
                  showClose: true,
                  timeout: 3000
              });
              this.router.navigate(["/register"]);
          }
      })



  }

}



