import { Component, OnInit } from '@angular/core';
import { RegionalService } from '../../services/regional.service';
import { SparYukService } from '../../services/spar-yuk.service';
import { ToastyService, ToastyConfig } from 'ng2-toasty';
import { Router } from '@angular/router';

import { TeamsFilterPipe } from '../../filters/teams-filter.pipe';

@Component({
  selector: 'app-spar-yuk',
  templateUrl: './spar-yuk.component.html',
  styleUrls: ['./spar-yuk.component.css']
})
export class SparYukComponent implements OnInit {
    teams: any;
    regional: string = "Semua";

    p: number = 1;
    region: any;

    // property untuk Team Away
    id_away_team: any;
    away_team: string;
    away_regional: string;
    away_email: string;
  constructor(
      private router: Router,
      private regionalService: RegionalService,
      private sparYukService: SparYukService,
      private toastMsg: ToastyService,
      private toastConf: ToastyConfig) { this.toastConf.theme = 'default' }

  ngOnInit() {
      this.loadRegional();
      this.getTeams();
  }

  loadRegional(){
      this.regionalService.loadRegional().subscribe(data => {
          this.region = data.regional;
      })
  }

  onChange(newValue){
      this.regional = newValue;
  }

  getTeams(){
      this.sparYukService.getAllTeam().subscribe(data => {
            this.teams = data.teams;
            console.log(data)
      });
  }

  konfirmasi(teamId, teamName, teamRegional, teamEmail){
      this.id_away_team = teamId;
      this.away_team = teamName;
      this.away_regional = teamRegional;
      this.away_email = teamEmail;
  }

  sparKuy(){
      const offer = {
          away_team: this.away_team,
          id_away_team: this.id_away_team,
          away_regional: this.away_regional,
          away_email: this.away_email
      }

      this.sparYukService.sparYuk(offer).subscribe(data => {
          if(data.success){
              this.toastMsg.success({
                  title: 'Berhasil',
                  msg: data.msg,
                  showClose: true,
                  timeout: 3000
              });
              this.router.navigate(['/dashboard'])
          } else {
              this.toastMsg.error({
                  title: 'Oopss..!',
                  msg: data.msg,
                  showClose: true,
                  timeout: 3000
              });
          }
      })
  }

}



