import { Component, OnInit } from '@angular/core';
import { SparingBoardService } from '../../services/sparing-board.service';

@Component({
  selector: 'app-sparing-board',
  templateUrl: './sparing-board.component.html',
  styleUrls: ['./sparing-board.component.css']
})
export class SparingBoardComponent implements OnInit {
    matches: any;
  constructor(private sparingBoard: SparingBoardService) { }

  ngOnInit() {
      this.loadMatches();
  }

  loadMatches(){
      this.sparingBoard.loadAllMatch().subscribe(data => {
          this.matches = data.match;
      })
  }

}



