import { Component, OnInit } from '@angular/core';
import { AuthService }  from '../../services/auth.service';
import { Router } from '@angular/router';
import { ToastyService, ToastyConfig} from 'ng2-toasty';
import { RegionalService } from '../../services/regional.service';
import { ValidateService } from '../../services/validate.service';


@Component({
  selector: 'app-profile',
  templateUrl: './profile.component.html',
  styleUrls: ['./profile.component.css']
})
export class ProfileComponent implements OnInit {
  user: object;
  userOnEdit: object;

  // Input property
  email: string;
  name: string;
  contact: string;
  usia_pemain: string;
  team_agent: string;
  regional: any;

  region: any;

  constructor(private authService: AuthService, private router: Router,
      private toastMsg: ToastyService,
      private toastConf: ToastyConfig,
      private regionalService: RegionalService,
      private validateService: ValidateService) { this.toastConf.theme = "default"; }

  ngOnInit() {
      this.LoadRegional();
      this.loadProfile();
  }

  loadProfile(){
    this.authService.getProfile().subscribe(profile => {
          this.user = profile.user;
      },
      err => {
          this.toastMsg.warning({
                  title: "Oops..!",
                  msg: "Kamu Belum Login!",
                  showClose: true,
                  timeout: 3000
              });
          this.router.navigate(['/login']);
          return false;
      })
  }

  LoadRegional(){
    this.regionalService.loadRegional().subscribe(data => {
      this.region = data.regional;
    })
  }

  onEditProfile(user){
    this.name = user.name;
    this.email = user.email;
    this.contact = user.contact;
    this.usia_pemain = user.usia_pemain;
    this.team_agent = user.team_agent;
    this.regional = user.regional;
  }

  updateProfile(){
    let newData = {
      name: this.name,
      email: this.email,
      contact: this.contact,
      usia_pemain: this.usia_pemain,
      team_agent: this.team_agent,
      regional: this.regional
    }

    if(!this.validateService.validateEditForm(newData)){
      this.toastMsg.error({
        title: "Oops..!",
        msg: "Lengkapi semua Formulir",
        showClose: true,
        timeout: 3500
      });
      return false;
    }

    if(!this.validateService.validateEmail(newData.email)){
      this.toastMsg.error({
        title: "Oops..!",
        msg: "Masukan Email yang Valid!",
        showClose: true,
        timeout: 3500
      });
      return false;
    }

    this.authService.updateDataUser(newData).subscribe(data => {
      if(data.success){
        this.toastMsg.success({
          title: "Success",
          msg: data.msg,
          showClose: true,
          timeout: 3000
        });
        setTimeout(() => {
          this.loadProfile();
        }, 500)

      } else {
        this.toastMsg.error({
          title: "Oops..!",
          msg: data.msg,
          showClose: true,
          timeout: 3000
        });
        this.router.navigate(['/profile']);
      }
    })

  }

}



