import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import 'rxjs/add/operator/map';

@Injectable()
export class RegionalService {
  constructor(private http: Http) { }

  loadRegional(){
      return this.http.get('http://localhost:8080/regional')
          .map(res => res.json());
  }
}



